.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: -18px;
  z-index: 1000;

  display: none;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;

  background-color: rgba($color-black-pearl, 0.8);

  transition: opacity $trans-modal,
    transform $trans-modal,
    visibility $trans-modal;
  pointer-events: none;
  -ms-overflow-style: none;
}

.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 146px 40px;
}

.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.modal__content {
  position: relative;
  width: 500px;
  min-height: 610px;
  padding: 40px;
  padding-top: 37px;
  color: $color-default-white;
  background-color: $color-deep-sky-blue;

  @include vp-767 {
    padding: 20px;
  }
}

.modal__close-btn {
  position: absolute;
  top: 15px;
  right: 14px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: $color-transparent;
  border: none;
  cursor: pointer;

  &:hover {
    opacity: 0.7;
    background-color: $color-darken-black-pearl--15;
  }
}

.modal__form {
  display: grid;
  grid-template-columns: 1fr;
  row-gap: 1px;
  margin-top: 17px;
}

.modal__close-btn::before,
.modal__close-btn::after {
  position: absolute;
  content: "";
  top: 19px;
  right: 7px;
  width: 27px;
  height: 1px;
  background-color: $color-default-white;
}

.modal__close-btn::before {
  transform: rotate(134deg);
}

.modal__close-btn::after {
  transform: rotate(47deg);
}

.modal__content h2 {
  margin: 0;
  font-family: $ff-roboto-condensed;
  font-weight: 400;
  font-size: 1.88rem;
  line-height: 140%;

  @include vp-767 {
    margin-right: 35px;
  }
}

.modal__content p {
  margin-top: 5px;
  margin-right: 30px;
  line-height: 138%;
}

.modal__agreement {
  margin-top: 12px;
}

.modal__form button {
  margin-top: 18px;
  margin-right: 0;
  padding-top: 15px;
  padding-bottom: 8px;
}

.modal__agreement span {
  margin-left: 34px;
  font-size: 0.88rem;
}

.modal__agreement .control-mark::before {
  top: 3px;
  left: 9px;
}

.modal__agreement .modal__svg {
  top: 8px;
  left: 12px;
}

.modal__agreement input:checked ~ .modal__svg {
  opacity: 1;
}

.modal__agreement input:focus ~ .control-mark::before {
  border: 1px solid $color-black-pearl;
}

.main .modal__close-btn {
  display: none;
}
